// ** React Imports
import { Link } from 'react-router-dom'

// ** Third Party Components
import Proptypes from 'prop-types'
import { ArrowLeft } from 'react-feather'
import { Breadcrumb, BreadcrumbItem, Button } from 'reactstrap'

import { randomString } from '@utils/randomString'

const CarhootBreadCrumbs = props => {
  // ** Props
  const { breadCrumbTitle, breadCrumbItems } = props

  const renderBreadCrumbItems = () => {
    const crumbItems =  breadCrumbItems.map((breadCrumbItem) => {
      const { link, title } = breadCrumbItem
      return (
        <BreadcrumbItem key={randomString(12)} tag='li'>
          {link && (<Link to={link}>{title}</Link>)}
          {(!link || link === '') && title}
        </BreadcrumbItem>
      )
    })
    return crumbItems
  }

  return (
    <div className='content-header row'>
      <div className='content-header-left col-md-9 col-12 mb-2'>
        <div className='row breadcrumbs-top'>
          <div className='col-12'>
            {breadCrumbTitle ? <h2 className='content-header-title float-left mb-0'>{breadCrumbTitle}</h2> : ''}
            <div className='breadcrumb-wrapper vs-breadcrumbs d-sm-block d-none col-12'>
              <Breadcrumb>
                {
                  renderBreadCrumbItems()
                }
              </Breadcrumb>
            </div>
          </div>
        </div>
      </div>
      <div className='content-header-right text-md-right col-md-3 col-12 d-md-block d-none'>
        <div className='form-group breadcrum-right dropdown'>
          <Button.Ripple className='btn-icon' color='primary'>
            <ArrowLeft size={16} />
          </Button.Ripple>
        </div>
      </div>
    </div>
  )
}
export default CarhootBreadCrumbs

// ** Default Props
CarhootBreadCrumbs.defaultProps = {
  breadCrumbTitle: '',
  breadCrumbItems: []
}
// ** PropTypes
CarhootBreadCrumbs.propTypes = {
  breadCrumbTitle: Proptypes.string.isRequired,
  breadCrumbItems: Proptypes.array.isRequired
}
