// ** React Imports
import { useState } from 'react'
import { Link, useHistory } from 'react-router-dom'

// ** Custom Components
import UserViewAvatar from '@components/user-avatar/UserViewAvatar'
import Avatar from '@components/avatar'

// ** Third Party Components
import { Button, Card, CardBody, Col, Media, Modal, ModalBody, ModalFooter, ModalHeader, Row } from 'reactstrap'
import { Check } from 'react-feather'

// ** Store Actions
import { useDispatch, useSelector } from 'react-redux'
import { deactivateAutoServiceCategory, deleteAutoServiceCategory } from '@store/AutoServiceCategory/actions'

const AutoServiceCategoryInfoCard = ({ selectedData }) => {
  const history = useHistory()
  const [disabledModal, setDisabledModal] = useState(false)
  const [modalTitle, setModalTitle] = useState('')
  const [modalDesc, setModalDesc] = useState('')
  const [action, setAction] = useState('')

  const dispatch = useDispatch()
  const { _id, serviceName, serviceCategory, serviceSubCategory, serviceIconUrl, basePrice } = selectedData

  const handleConfirm = () => {
    setDisabledModal(false)
    if (action === 'deactivate') {
      dispatch(deactivateAutoServiceCategory(_id))
    } else if (action === 'delete') {
      dispatch(deleteAutoServiceCategory(_id))
      history.push("/autoServiceCategory/list")
    }
  }

  return (
    <>
      <Card>
        <CardBody>
          <Row>
            <Col xl='4' lg='12' className='d-flex flex-column justify-content-between border-container-lg'>
              <UserViewAvatar
                name={serviceName}
                photo={''}
              />
            </Col>
            <Col xl='3' lg='12' className='mt-2 mt-xl-0'>
              <div className='user-info-wrapper'>
                <Media className='mt-2'>
                  <Avatar color='light-primary' className='rounded mr-1' icon={<Check size={18} />} />
                  <Media body>
                    <h6 className='mb-0'>Category</h6>
                    <small>{serviceCategory}</small>
                  </Media>
                </Media>
                <Media className='mt-2'>
                  <Avatar color='light-primary' className='rounded mr-1' icon={<Check size={18} />} />
                  <Media body>
                    <h6 className='mb-0'>Subcategory</h6>
                    <small>{serviceSubCategory}</small>
                  </Media>
                </Media>
                <Media className='mt-2'>
                  <Avatar color='light-primary' className='rounded mr-1' icon={<Check size={18} />} />
                  <Media body>
                    <h6 className='mb-0'>Icon Url</h6>
                    <small>{serviceIconUrl}</small>
                  </Media>
                </Media>
                <Media className='mt-2'>
                  <Avatar color='light-primary' className='rounded mr-1' icon={<Check size={18} />} />
                  <Media body>
                    <h6 className='mb-0'>Price</h6>
                    <small>{basePrice}</small>
                  </Media>
                </Media>
              </div>
            </Col>
            <Col xl='2' lg='12' className='mt-2 mt-xl-0'>
              <div className='d-flex flex-wrap align-items-center my-50'>
                <Button.Ripple
                  block
                  color='primary'
                  tag={Link} to={`/autoServiceCategory/edit/${_id}`}>
                  Edit
                </Button.Ripple>
              </div>
              <div className='d-flex flex-wrap align-items-center my-50'>
                <Button.Ripple
                  block
                  outline
                  onClick={() => {
                    setAction('deactivate')
                    setModalTitle('Deactivate User')
                    setModalDesc('Are you sure you want to deactivate the user')
                    setDisabledModal(true)
                  }}
                  color='danger'>
                  Deactivate
                </Button.Ripple>
              </div>
              <div className='d-flex flex-wrap align-items-center my-50'>
                <Button.Ripple
                  block
                  color='danger'
                  onClick={() => {
                    setAction('delete')
                    setModalTitle('Delete User')
                    setModalDesc('Are you sure you want to delete the user')
                    setDisabledModal(true)
                  }}
                  outline>
                  Delete
                </Button.Ripple>
              </div>
            </Col>
          </Row>
        </CardBody>
      </Card>
      <Modal
        isOpen={disabledModal}
        toggle={() => setDisabledModal(!disabledModal)}
      >
        <ModalHeader toggle={() => setDisabledModal(!disabledModal)}>{modalTitle}</ModalHeader>
        <ModalBody>
          {modalDesc}
        </ModalBody>
        <ModalFooter>
          <Button color='primary' onClick={handleConfirm}>
            Yes
          </Button>
          <Button color='primary' onClick={() => setDisabledModal(!disabledModal)} outline>
            No
          </Button>
        </ModalFooter>
      </Modal>
    </>
  )
}

export default AutoServiceCategoryInfoCard
