import PropTypes from "prop-types"
import Avatar from '@components/avatar'
import { CardText } from "reactstrap"

const UserAvatar = (props) => {
  const { name, photo } = props
  if (photo !== undefined && photo.length) {
    return <img src={photo} alt='user-avatar' className='img-fluid rounded' height='104' width='104' />
  } else {
    const stateNum = Math.floor(Math.random() * 6),
      states = ['light-success', 'light-danger', 'light-warning', 'light-info', 'light-primary', 'light-secondary'],
      color = states[stateNum]
    return (
      <Avatar
        initials
        color={color}
        className='rounded'
        content={name}
        contentStyles={{
          borderRadius: 0,
          fontSize: 'calc(36px)',
          width: '100%',
          height: '100%'
        }}
        style={{
          height: '90px',
          width: '90px'
        }}
      />
    )
  }
}

const UserViewAvatar = (props) => {
  const { name, photo, email } = props
  return (
    <div className='user-avatar-section'>
      <div className='d-flex justify-content-start'>
        <UserAvatar
          name={name}
          photo={photo}
        />
        <div className='d-flex flex-column ml-1'>
          <div className='user-info mb-1'>
            {name && name.length && (<h4 className='mb-0'>{name}</h4>)}
            {email && email.length && (<CardText tag='span'>{email}</CardText>)}
          </div>
        </div>
      </div>
    </div>
  )
}

export default UserViewAvatar

// ** Default Props
UserViewAvatar.defaultProps = {
  name: '',
  photo: '',
  email: ''
}

// ** PropTypes
UserViewAvatar.propTypes = {
  name: PropTypes.string.isRequired,
  photo: PropTypes.string,
  email: PropTypes.string
}

