// ** React Imports
import { useEffect } from 'react'
import { Link, useParams } from 'react-router-dom'

// ** Store & Actions
import { getAutoServiceCategory } from '@store/AutoServiceCategory/actions'
import { useDispatch, useSelector } from 'react-redux'

// ** Reactstrap
import { Alert, Col, Row } from 'reactstrap'

// // ** UserCars View Components
import AutoServiceCategoryInfoCard from './InfoCard'

// ** Custom Components
import UILoader from '@components/ui-loader'
import CarhootBreadCrumbs from '@components/breadcrumbs/CarhootBreadCrumbs'

// ** Styles
import '@styles/react/apps/app-users.scss'

const AutoServiceCategoryView = () => {
  // ** Vars
  const store = useSelector(state => state.autoServiceCategories),
    dispatch = useDispatch(),
    { id } = useParams()
  const { isLoading } = store

  // ** Get suer on mount
  useEffect(() => {
    dispatch(getAutoServiceCategory(id))
  }, [dispatch])

  return store.selectedData !== null && store.selectedData !== undefined ? (
    <UILoader blocking={isLoading}>
      <CarhootBreadCrumbs
        breadCrumbTitle='Breadcrumbs'
        breadCrumbItems={
          [
            {
              link: `/autoServiceCategory/list`,
              title: 'Auto Service Category'
            },
            {
              title: 'Auto Service Detail'
            }
          ]
        }
      />
      <div className='app-user-view'>
        <Row>
          <Col xl='12'>
            <AutoServiceCategoryInfoCard selectedData={store.selectedData} />
          </Col>
        </Row>
      </div>
    </UILoader>
  ) : (
    <Alert color='danger'>
      <h4 className='alert-heading'>AutoServiceCategory not found</h4>
      <div className='alert-body'>
        AutoServiceCategory with id: {id} doesn't exist. Check list of all AutoServiceCategories: <Link to='/autoServiceCategory/list'>AutoServiceCategories List</Link>
      </div>
    </Alert>
  )
}

export default AutoServiceCategoryView
