/*
 *
 * AutoServiceCategory actions
 *
 */

import axios from 'axios'
import { success } from "react-notification-system-redux"

import {
  RESET_FORM,
  GET_ALL_DATA,
  GET_DATA,
  GET,
  ADD,
  DELETE,
  SET_LOADING,
  SET_SUBMITTING,
  SET_FORM_ERRORS,
  FORM_CHANGE,
  EDIT_CHANGE,
  DEACTIVATE
} from './constants'
import handleError from '@utils/error'
import { allFieldsValidation } from '@utils/validation'

export const autoServiceCategoryFormChange = (name, value) => {
  const formData = {}
  formData[name] = value

  return {
    type: FORM_CHANGE,
    payload: formData
  }
}

export const autoServiceCategoryEditChange = (name, value) => {
  const formData = {}
  formData[name] = value

  return {
    type: EDIT_CHANGE,
    payload: formData
  }
}

// ** Get all Data
export const getAllAutoServiceCategories = () => {
  return async dispatch => {
    await axios.get('/api/autoServiceCategories').then(response => {
      dispatch({
        type: GET_ALL_DATA,
        data: response.data.autoServiceCategories
      })
    })
  }
}

// ** Get data on page or row change
export const getAutoServiceCategoryList = params => {
  return async dispatch => {
    await axios.get('/api/autoServiceCategories', { params }).then(response => {
      dispatch({
        type: GET_DATA,
        data: response.data.autoServiceCategories,
        totalPages: response.data.total,
        params
      })
    })
  }
}

// ** Get AutoServiceCategory
export const getAutoServiceCategory = id => {
  return async dispatch => {
    await axios
      .get(`/api/autoServiceCategories/${id}`)
      .then(response => {
        dispatch({
          type: GET,
          selectedData: response.data.autoServiceCategory
        })
      })
      .catch(err => console.log(err))
  }
}

// ** Add autoServiceCategory
export const addAutoServiceCategory = () => {
  return async (dispatch, getState) => {
    const rules = {
      serviceName: 'required',
      serviceCategory: 'required',
      serviceSubCategory: 'required',
      serviceIconUrl: 'required',
      basePrice: 'required'
    }

    const autoServiceCategory = getState().autoServiceCategories.formData
    const { isValid, errors } = allFieldsValidation(autoServiceCategory, rules, {
      'required.serviceName': 'Service name is required.',
      'required.serviceCategory': 'Category is required.',
      'required.serviceSubCategory': 'Subcategory is required.',
      'required.serviceIconUrl': 'Icon Url is required.',
      'required.basePrice': 'Base price is required.'
    })

    if (!isValid) {
      return dispatch({ type: SET_FORM_ERRORS, payload: errors })
    }

    dispatch({ type: SET_SUBMITTING, payload: true })
    dispatch({ type: SET_LOADING, payload: true })

    try {
      const response = await axios.post('/api/autoServiceCategories', autoServiceCategory)
      dispatch({
        type: ADD,
        autoServiceCategory
      })
      dispatch({
        type: RESET_FORM,
        payload: {}
      })
      dispatch(getAutoServiceCategoryList(getState().autoServiceCategories.params))
      dispatch(getAllAutoServiceCategories())

      const successfulOptions = {
        title: `The autoServiceCategory has been added successfully!`,
        position: 'tr',
        autoDismiss: 1
      }

      dispatch(success(successfulOptions))
    } catch (error) {
      const title = `We were not able to create the autoServiceCategory. Please try again.`
      handleError(error, dispatch, title)
    } finally {
      dispatch({ type: SET_SUBMITTING, payload: false })
      dispatch({ type: SET_LOADING, payload: false })
    }
  }
}

// ** Delete autoServiceCategory
export const updateAutoServiceCategory = (_id) => {
  return async (dispatch, getState) => {
    const rules = {
      serviceName: 'required',
      serviceCategory: 'required',
      serviceSubCategory: 'required',
      serviceIconUrl: 'required',
      basePrice: 'required'
    }

    const autoServiceCategory = { ...getState().autoServiceCategories.selectedData }
    const { isValid, errors } = allFieldsValidation(autoServiceCategory, rules, {
      'required.serviceName': 'Service name is required.',
      'required.serviceCategory': 'Category is required.',
      'required.serviceSubCategory': 'Subcategory is required.',
      'required.serviceIconUrl': 'Icon Url is required.',
      'required.basePrice': 'Base price is required.'
    })


    if (!isValid) {
      return dispatch({ type: SET_FORM_ERRORS, payload: errors })
    }

    dispatch({ type: SET_SUBMITTING, payload: true })
    dispatch({ type: SET_LOADING, payload: true })

    try {
      delete autoServiceCategory._id
      delete autoServiceCategory.createdAt
      delete autoServiceCategory.updatedAt
      delete autoServiceCategory.userInfo
      delete autoServiceCategory.__v
      await axios.put(`/api/autoServiceCategories/${_id}`, autoServiceCategory)
      dispatch({
        type: RESET_FORM,
        payload: {}
      })

      const successfulOptions = {
        title: `AutoServiceCategory information was updated successfully!`,
        position: 'tr',
        autoDismiss: 1
      }

      dispatch(success(successfulOptions))
    } catch (error) {
      const title = `We were not able to update the autoServiceCategory. Please try again.`
      handleError(error, dispatch, title)
    } finally {
      dispatch({ type: SET_SUBMITTING, payload: false })
      dispatch({ type: SET_LOADING, payload: false })
    }
  }
}

// ** Delete autoServiceCategory
export const deactivateAutoServiceCategory = id => {
  return async (dispatch, getState) => {
    try {
      const response = await axios.post(`/api/autoServiceCategories/${id}/deactivate`)
      if (response.data.success === true) {
        const successfulOptions = {
          title: `${response.data.message}`,
          position: 'tr',
          autoDismiss: 1
        }
        dispatch(success(successfulOptions))
        dispatch({
          type: DEACTIVATE
        })
      }
    } catch (error) {
      const title = `We were not able to delete the autoServiceCategory. Please try again.`
      handleError(error, dispatch, title)
    } finally {
      dispatch({ type: SET_SUBMITTING, payload: false })
      dispatch({ type: SET_LOADING, payload: false })
    }
  }
}

// ** Delete autoServiceCategory
export const deleteAutoServiceCategory = id => {
  return async (dispatch, getState) => {
    try {
      const response = await axios.delete(`/api/autoServiceCategories/${id}`)
      if (response.data.success === true) {
        const successfulOptions = {
          title: `${response.data.message}`,
          position: 'tr',
          autoDismiss: 1
        }
        dispatch(success(successfulOptions))
        dispatch({
          type: DELETE
        })
      }
    } catch (error) {
      const title = `We were not able to delete the autoServiceCategory. Please try again.`
      handleError(error, dispatch, title)
    } finally {
      dispatch({ type: SET_SUBMITTING, payload: false })
      dispatch({ type: SET_LOADING, payload: false })
    }
  }
}
